@font-face {
    font-family: 'Merriweather Web';
    src: url('../../assets/fonts/Merriweather/fonts/');
    src: url('../../assets/fonts/Merriweather/fonts/?#iefix') format('embedded-opentype'),
         url('../../assets/fonts/Merriweather/fonts/merriweather-light-webfont.woff2') format('woff2'),
         url('../../assets/fonts/Merriweather/fonts/merriweather-light-webfont.woff') format('woff'),
         url('../../assets/fonts/Merriweather/fonts/merriweather-light-webfont.ttf') format('truetype'),
         url('../../assets/fonts/Merriweather/fonts/merriweather-light-webfont.svg#merriweatherlight') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  
